<template>
    <div class="container-fluid">
        <div class="row justify-content-center">
            <div class="col-md-4">
                <h5 class="">Customize Volunteer List</h5>
                <div v-if="!showEditInput" class="card border-0 card-body">
                        
                    <p>
                        <button @click="caretUp=!caretUp" class="btn btn-primary" type="button" data-bs-toggle="collapse" data-bs-target="#collapseExample" aria-expanded="false" aria-controls="collapseExample">
                          Choose CW or GD <i class="fa-solid ms-2" :class="caretUp?' fa-caret-up':' fa-caret-down'"></i>
                        </button>
                      </p>
                      <div class="collapse" id="collapseExample">
                        <div class="row justify-content-center">
                            <div class="col-8">
                                <div class="card card-body mb-3 bg-light ">
                                    <div class="form-check d-flex">
                                        <input v-model="option" value="gd" class="form-check-input me-3" type="radio" name="flexRadioDefault" id="flexRadioDefault1">
                                        <label class="form-check-label fw-semibold" for="flexRadioDefault1">
                                          Graphic Designer
                                        </label>
                                      </div>
                                      <div class="form-check d-flex ">
                                        <input v-model="option" value="cw" class="form-check-input me-3" type="radio" name="flexRadioDefault" id="flexRadioDefault2">
                                        <label class="form-check-label fw-semibold" for="flexRadioDefault2">
                                          Content Writer
                                        </label>
                                      </div>                                  
                                </div>
                            </div>
                        </div>
                      </div>
                      <button @click="showEditInput=true" class="btn btn-outline-primary w-75 mx-auto" :hidden="option===null"><i class="fa-regular fa-circle-right me-2"></i>Enter</button>
                </div>
                <div v-if="showEditInput" >
                    <div class="p-3">
                        <single-edit-input @show-edit-input="handleShowEditInput" :option="option"></single-edit-input>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import SingleEditInput from '../components/SingleEditInput';

   export default { 
    name : 'EditView',
    components: {
        SingleEditInput,
    },
    data() {
        return {
            caretUp : false,
            option: null,
            showEditInput: false,
        }
    },
    methods: {
        handleShowEditInput(value) {
            this.showEditInput = value;
            this.caretUp = !(this.caretUp);
        }
    },
   }
</script>

