<template>
    <div class="">
        <form>
            <div class="row mt-4">
                <div class="input-gp mb-3" v-for="(vol,index) in volList" :key="index">
                  <input v-model="newList[index]" required class="input" type="text">
                  <label v-if="option == 'gd'" class="label" for="" >GD Name</label>
                  <label v-if="option == 'cw'" class="label" for="" >CW Name</label>
                  <div @click="addVol" v-show="index== volList.length - 1" class="plus-icon"><i
                      class="bi bi-plus-circle-fill" ></i>
                    </div>
                </div>
            </div>
            <div class="d-flex justify-content-start">
                <button @click="storeList" class="btn btn-primary me-2"><i class="fa-solid me-2 fa-user-check"></i>Confirm</button>
                <button :disabled="newList.length==1" @click="removeLast" class="btn btn-outline-primary border border-2 border-primary"><i class="bi bi-person-dash"></i></button>
            </div>
          </form>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import Swal from 'sweetalert2/dist/sweetalert2.min.js';


export default {
    name : 'SingleEditInput',
    data() {
        return {
            newList : [],
        }
    },
    created () {
        this.getVolList();
    },
    computed: {
        volList() {
            if(this.option == 'gd'){
                return  this.$store.getters.getGdList; 
            }else if(this.option == 'cw'){
                return this.$store.getters.getCwList; 
            }
            return this.$store.getters.getGdList; 

        }
    },
    methods: {
        getVolList(){
            if(this.option == 'gd'){
                this.newList = this.$store.getters.getGdList;
            }else if(this.option == 'cw'){
                this.newList = this.$store.getters.getCwList;
            }
        },
        addVol() {
            this.newList.push('');
        },
        removeLast(){
            this.newList.pop();
        },
        storeList(){
            if(this.option == 'gd'){
                this.$store.commit("addGdList",this.newList);
            }else if(this.option == 'cw'){
                this.$store.commit("addCwList",this.newList);
            }
            Swal.fire({
                title: 'Halo',
                text: "Volunteer List updated!",
                icon: 'success',
                confirmButtonColor: '#0d6efd',
            })
            this.$emit('show-edit-input',false);
            
        }
    },
    props: {
        option: {
            type: String,
        },
    },
}

</script>


<style>

.input-gp {
    position: relative;
}

.input {
    width: 100%;
    border: 1.2px solid gray;
    border-radius:5px;
    padding : 1rem;
    font-size: 1rem;
    transition: border 150ms cubic-bezier(0.4,0,0.2,1);
}

.label {
    position: absolute;
    left: 28px;
    font-weight: 600;
    pointer-events: none;
    transform: translateY(1rem);
    transition: 150ms cubic-bezier(0.4,0,0.2,1);
}

.input:focus{
  outline: none;
  border: 2px solid #3a0ca3;
}

.input:focus ~ .label,input:valid ~ .label  {
    transform: translateY(-50%) scale(0.8);
    background-color: white;
    padding: 0.4em;
    font-weight: 600;
    color :#3a0ca3
}

.plus-icon {
    position: absolute;
    top: 50%;
    right: 20px;
    font-size: 28px;
    transform: translateY(-50%);
    cursor: pointer;
}

</style>