import { createStore } from 'vuex'

export default createStore({
  state: {
    totalReviews: [] as any[] ,
    gdList : [''] as any[],
    cwList : [''] as any[],
  },
  getters: {
    getGdList(state){
      const localGdList = localStorage.getItem('gdList');
      if(localGdList){
        state.gdList = JSON.parse(localGdList);
      }
      return state.gdList;
    },
    getCwList(state){
      const localCwList = localStorage.getItem('cwList');
      if(localCwList){
        state.gdList = JSON.parse(localCwList);
      }
      return state.cwList;
    },
    getReviews (state) {
      const localReviews = localStorage.getItem('totalReviews');
      if(localReviews){
        state.totalReviews = JSON.parse(localReviews);
      }
      return state.totalReviews;
    },
  },
  mutations: {
    addGdList(state,value:any){
      state.gdList = value;
      localStorage.setItem('gdList',JSON.stringify(state.gdList));
    },
    addCwList(state,value:any){
      state.cwList = value;
      localStorage.setItem('cwList',JSON.stringify(state.cwList));
    },

    // },
    pushReview(state, value: any) {
      //first check if local storage has data ?
      const localReviews = localStorage.getItem('totalReviews');
      if(localReviews){
        state.totalReviews = JSON.parse(localReviews);
      }
      
      state.totalReviews.push(value);
      localStorage.setItem('totalReviews',JSON.stringify(state.totalReviews));
    },
    deleteAll(state){
      state.totalReviews = [];
      localStorage.setItem('totalReviews',JSON.stringify(state.totalReviews));
    },
    removeRow(state,index){
      state.totalReviews.splice(index,1);
      localStorage.setItem('totalReviews',JSON.stringify(state.totalReviews));
    }
  },
  actions: {
    
  },
  modules: {
  }
})
